<template>
  <div>
    <div class="comp-detail-panel">
      <div class="panel-img">
        <img src="../assets/sea.jpg" alt="" />
      </div>
      <div class="panel-info">
        <div>
          <h1>{{ competition.name }}</h1>
        </div>
        <div>
          <h3>{{ competition.address }}</h3>
        </div>
        <div class="time">
          报名时间段：{{ competition.signUpTimeFrom }} -
          {{ competition.signUpTimeTo }}
        </div>
        <div class="time">
          比赛时间段：{{ competition.holdTimeFrom }} -
          {{ competition.holdTimeTo }}
        </div>
        <div class="options">
          <el-button
            size="medium"
            type="primary"
            @click="enrollFor(competition.id)"
          >
            报名参赛
          </el-button>
          <el-button size="medium">下载比赛章程</el-button>
        </div>
      </div>
    </div>

    <div>
      <el-collapse v-model="activeNames" @change="handleChange">
        <el-collapse-item title="比赛日程安排" name="schedule">
          <el-empty description="暂时没有安排" :image-size="180"></el-empty>
        </el-collapse-item>
        <el-collapse-item title="项目列表" name="types">
          <el-empty description="暂时没有项目" :image-size="180"></el-empty>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>

<script>
import { getDetail } from "../api/competitions";

export default {
  name: "Competition",
  props: {},
  data() {
    return {
      competition: "",
      activeNames: ["schedule"],
    };
  },
  created() {
    const id = this.$route.query.id;
    if (id) {
      getDetail(id).then((data) => {
        this.competition = data;
      });
    }
  },
  methods: {
    enrollFor(id) {
      this.$router.push({
        name: "enroll",
        query: { id },
      });
    },
    handleChange(val) {
      console.log(val);
    },
  },
};
</script>


<style scoped>
.comp-detail-panel {
  display: flex;
  padding: 20px 0;
  border-bottom: 1px lightgray solid;
}

.panel-img {
  flex: 0;
  height: 250px;
}

.panel-img > img {
  height: 100%;
}

.panel-info {
  flex: 1;
  padding: 0 40px;
}

.time {
  line-height: 30px;
}

.options {
  margin: 10px 0;
}

</style>
