import request from '@/utils/request'
import { localCompetitions } from "../mock/competitions"


export function getList(page = 0, num = 10) {
    return new Promise((resolve, reject) => {
        request({
            url: '/vue-element-admin/competitions',
            method: 'get',
            params: { page, num }
        }).then(response => {
            const { data } = response
            resolve(data)
        }).catch(error => {
            if (error) {
                const data = localCompetitions.getList(page, num)
                resolve(data)
            }
            else reject(error)
        })
    })
}

export function getDetail(id) {
    return new Promise((resolve, reject) => {
        request({
            url: '/vue-element-admin/detail',
            method: 'get',
            params: { id }
        }).then(response => {
            const { data } = response
            resolve(data)
        }).catch(error => {
            if (error) {
                const data = localCompetitions.getDetail(id)
                resolve(data)
            }
            else reject(error)
        })
    })
}
