function mockCompetitions() {

    const nowDate = new Date()

    function addDays(days = 0) {
        const date = new Date()
        date.setDate(nowDate.getDate() + days);
        return date.toISOString().slice(0, 10)
    }

    function generate(index) {
        return {
            id: index,
            name: "赛事名称 - " + index,
            address: "比赛地点 - " + index,
            image: "../assets/logo.png",
            signUpTimeFrom: addDays(index + 1),
            signUpTimeTo: addDays(index + 10),
            holdTimeFrom: addDays(index + 15),
            holdTimeTo: addDays(index + 20),
        }
    }


    let competitions = {}

    for (let index = 1; index < 21; index++) {
        const comp = generate(index);
        competitions[index] = comp;
    }

    return {
        getList() {
            return Object.values(competitions).map((comp) => {
                const { id, name, address, image, signUpTimeFrom } = comp;
                return { id, name, address, image, signUpTimeFrom };
            });
        },

        getDetail(id) {
            return competitions[id]
        }
    }
}

export const localCompetitions = mockCompetitions()
